<template>
	<view class="board">
		<view class="back" style="font-weight:500;color:#1E8EEE;font-size:16px;cursor: pointer;" @click="back">
			<image src="/static/back_blue.png" mode="widthFix" style="width:18px;height:18px;margin-right:5px;">
			</image>
			<text>返回</text>
		</view>
		<view class="box flex flex-start" style="align-items: start;padding-left:0;">
			<view class="box_item">
				<view class="l_tabs">
					<!-- <view class="l_tab" :class="currentMainTab==0?'active':''" @click="changeMainTab(0)">工人情况</view> -->
					<view class="l_tab" :class="currentMainTab==1?'active':''" @click="changeMainTab(1)">项目详情</view>
				</view>
			</view>
			<view class="box_item box_right" v-if="currentMainTab == 0">
				<searchBox :showRange="true" :showEmployee="true" :timeStr="timeStr" @handleSearch="handleSearch">
				</searchBox>
				<view class="table_wrap">
					<view class="tabs flex flex_start">
						<view class="tab" v-for="(item,index) in tabs" :class="currentTab.value==item.value?'active':''"
							@click="changeTab(item)">{{item.text}}</view>
					</view>
					<view class="btns flex flex_end" v-if="currentTab.value==1">
						<view class="btn" @click="batchPass('',true)">批量通过</view>
						<view class="btn" @click="batchPass('',false)">批量拒绝</view>
					</view>
					<view class="btns flex flex_end" v-if="currentTab.value==2 && !currentInfo.contract_template_id">
						<view class="btn" @click="batchSign('',true)">批量签约</view>
						<view class="btn" @click="batchSign('',false)">批量未签</view>
					</view>
					<view class="btns flex flex_end" v-if="currentTab.value==3">
						<view class="btn" @click="batchCheckin('',true)">批量报到</view>
						<view class="btn" @click="batchCheckin('',false)">批量失约</view>
					</view>
					<view class="btns flex flex_end" v-if="currentTab.value==4">
						<view class="btn" @click="batchDepart">批量离职</view>
						<view class="btn" @click="batchPass(false)">导出</view>
					</view>
					<uni-table border stripe emptyText="暂无更多数据" style="height:500px;">
						<uni-tr style="background: #f6f6f6;">
							<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;"
								v-if="currentTab.value==1 || currentTab.value==2 || currentTab.value==3 || currentTab.value==4">
								<view class="flex flex_around">
									<image :src="seleAll?'/static/selected.png':'/static/unselect.png'" mode="widthFix"
										style="width:18px;height:18px;margin-right: 5px;" @click="checkAll"></image>
									<text>全选</text>
								</view>
							</uni-th>
							<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;">工人姓名</uni-th>
							<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;">身份证号</uni-th>
							<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;">手机号</uni-th>
							<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;">性别</uni-th>
							<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;">年龄</uni-th>
							<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;">民族</uni-th>
							<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;">状态</uni-th>
							<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;"
								v-if="currentTab.value == 0 || currentTab.value == 1 || currentTab.value == 2">报名时间</uni-th>
							<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;"
								v-if="currentTab.value == 3">签约时间</uni-th>
							<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;"
								v-if="currentTab.value == 4 || currentTab.value == 5">入职时间</uni-th>
							<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;"
								v-if="currentTab.value == 5">离职时间</uni-th>
							<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;">操作时间</uni-th>
							<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;"
								v-if="currentTab.value != 2">操作</uni-th>
						</uni-tr>
						<uni-tr v-for="(item,index) in list" :key="index">
							<uni-td align="center"
								v-if="currentTab.value==1 || currentTab.value==2 || currentTab.value==3 || currentTab.value==4">
								<image
									:src="selectedIds.indexOf(item.id) == -1?'/static/unselect.png':'/static/selected.png'"
									mode="widthFix" style="width:18px;height:18px;margin-right: 5px;"
									v-if="item.status != 'signing_expired' && item.status != 'rejected'"
									@click="addToList(item.id)"></image>
							</uni-td>
							<uni-td align="center">
								<view style="cursor: pointer;color:#1E8EEE;" @click="showWorkerDetail(item)">
									{{item.worker_name}}
								</view>
							</uni-td>
							<uni-td align="center">{{item.worker_id_card_number}}</uni-td>
							<uni-td align="center">{{item.worker_mobile}}</uni-td>
							<uni-td align="center">{{item.worker_gender == "male"?"男":"女"}}</uni-td>
							<uni-td
								align="center">{{new Date().getFullYear() - item.worker_birthday.slice(0,4)}}</uni-td>
							<uni-td align="center">{{item.worker_nation}}</uni-td>
							<uni-td align="center">{{item.source_broker_company_name}}</uni-td>
							<uni-td align="center">
								<view class="status"></view>
							</uni-td>
							<uni-td align="center">{{item.create_time}}</uni-td>
							<uni-td align="center">{{item.approved_time}}</uni-td>
							<uni-td align="center" v-if="currentTab.value == 5">{{item.approved_time}}</uni-td>
							<uni-td align="center" style="padding:8px 0px;" v-if="currentTab.value!=2">
								<view class="ope flex flex_around">
									<view class="ope_item" @click="batchPass(item.id,true)"
										v-if="item.status == 'pending'">
										通过</view>
									<view class="ope_item" @click="batchPass(item.id,false)"
										v-if="item.status == 'pending'">拒绝</view>
									<view class="ope_item" @click="batchCheckin(item.id,true)"
										v-if="item.status == 'contracted'">报到</view>
									<view class="ope_item" @click="batchCheckin(item.id,false)"
										v-if="item.status == 'contracted'">失约</view>
									<view class="ope_item" @click="showRecord(item)"
										v-if="item.status == 'entry' || item.status == 'depart'">考勤记录</view>
									<view v-if="item.status == 'signing_expired'">{{item.signing_expired_time}}</view>
									<view class="ope_item" @click="viewContract(item.id)"
										v-if="item.status == 'entry' || item.status == 'depart'">查看合同</view>
								</view>
							</uni-td>
						</uni-tr>

					</uni-table>
					<pageBox :page="pagination" @toNext="toNext"></pageBox>
				</view>
			</view>
			<view class="box_item box_right" v-if="currentMainTab == 1">
				<view class="title">项目详情</view>
				<view class="base flex flex_btween" style="align-items: start;flex-wrap: wrap;"
					:class="showMoreInfo?'showall':''">
					<view class="base_box" style="width:50%;">
						<view class="line flex flex_start">
							<view class="tit">项目名称：</view>
							<view class="text">{{currentInfo.name}}</view>
						</view>
						<view class="line flex flex_start">
							<view class="tit">工人薪资：</view>
							<view class="text">{{currentInfo.worker_salary}}</view>
						</view>
						<view class="line flex flex_start">
							<view class="tit">工作福利：</view>
							<view class="text">{{currentInfo.worker_welfare}}</view>
						</view>
						<view class="line flex flex_start">
							<view class="tit">工作地址：</view>
							<view class="text">{{currentInfo.work_address}}</view>
						</view>
					</view>
					<view class="base_box" style="width:50%;">
						<view class="line flex flex_start">
							<view class="tit">项目经理：</view>
							<view class="text">{{currentInfo.broker_name+"（"+currentInfo.broker_mobile+"）"}}</view>
						</view>
						<view class="line flex flex_start">
							<view class="tit">费用单价：</view>
							<view class="text">{{currentInfo.platform_fee_price}}元/人/时</view>
						</view>
						<view class="line flex flex_start">
							<view class="tit">结算方式：</view>
							<view class="text">周期性</view>
						</view>
					</view>
					<view class="line" style="width:100%;margin-top:0;">
						<view class="tit" style="margin-bottom: 15px;">项目描述：</view>
						<view class="text"
							style="line-height: 28px;white-space: pre-wrap;border:1px solid #ccc;border-radius: 5px;font-size: 14px;padding:10px;box-sizing: border-box;color:#666;">
							{{currentInfo.content}}
						</view>
					</view>
				</view>
			</view>

			<!-- 项目基本信息 -->

			<!-- <view class="showmore" @click="showMore" style="width:100%;">
				<view class="text">{{moreText}}</view>
				<image :src="showMoreInfo?'/static/zhedie_icon.png':'/static/more_icon.png'" mode="widthFix"></image>
			</view> -->

		</view>
		<view class="mask" v-if="showMask">
			<view class="inner" v-if="showFirst">
				<view class="title">办理离职</view>
				<view class="close" @click="close">
					<image src="/static/close_white.png" mode="widthFix"></image>
				</view>
				<view class="box">
					<view class="table_wrap">
						<uni-table border stripe emptyText="暂无更多数据" style="height:500px;">
							<uni-tr style="background: #f6f6f6;">
								<uni-th align="center"
									style="font-size: 14px;font-weight: 600;color:#333;">工人姓名</uni-th>
								<uni-th align="center"
									style="font-size: 14px;font-weight: 600;color:#333;">身份证号码</uni-th>
								<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;">手机号</uni-th>
								<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;">性别</uni-th>
								<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;">年龄</uni-th>
								<uni-th align="center"
									style="font-size: 14px;font-weight: 600;color:#333;">入职时间</uni-th>
								<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;">操作</uni-th>
							</uni-tr>
							<uni-tr v-for="(item,index) in waitDepartList" :key="index">
								<uni-td align="center">{{item.worker_name}}</uni-td>
								<uni-td align="center">{{item.worker_id_card_number}}</uni-td>
								<uni-td align="center">{{item.worker_mobile}}</uni-td>
								<uni-td align="center">{{item.worker_gender == "male"?"男":"女"}}</uni-td>
								<uni-td
									align="center">{{new Date().getFullYear() - item.worker_birthday.slice(0,4)}}</uni-td>
								<uni-td align="center">{{item.source_broker_company_name}}</uni-td>
								<uni-td align="center">{{item.create_time}}</uni-td>
								<uni-td align="center" style="padding:8px 0px;">
									<view class="ope flex flex_start">
										<view class="ope_item" @click="deleteData(item.id)">移除</view>
									</view>
								</uni-td>
							</uni-tr>
						</uni-table>
						<view class="btn" style="width:200px;float: right;margin-top:15px;" @click="sureDepart">提交离职办理申请
						</view>
						<!-- <pageBox :page="pagination"  @toNext="toNext"></pageBox> -->
					</view>
				</view>
			</view>
			<view class="inner" v-if="showSecond">
				<view class="title">工人{{currentWorker.worker_name}}的考勤记录</view>
				<view class="close" @click="close">
					<image src="/static/close_white.png" mode="widthFix"></image>
				</view>
				<view class="box">
					<view class="table_wrap">
						<uni-table border stripe emptyText="暂无更多数据" style="height:500px;">
							<uni-tr style="background: #f6f6f6;">
								<uni-th align="center"
									style="font-size: 14px;font-weight: 600;color:#333;">考勤编号</uni-th>
								<uni-th align="center"
									style="font-size: 14px;font-weight: 600;color:#333;">考勤方式</uni-th>
								<uni-th align="center"
									style="font-size: 14px;font-weight: 600;color:#333;">工作时长</uni-th>
								<uni-th align="center"
									style="font-size: 14px;font-weight: 600;color:#333;">开始时间</uni-th>
								<uni-th align="center"
									style="font-size: 14px;font-weight: 600;color:#333;">结束时间</uni-th>
								<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;">状态</uni-th>
							</uni-tr>
							<uni-tr v-for="(item,index) in workerCheckinList" :key="index">
								<uni-td align="center">{{item.id}}</uni-td>
								<uni-td align="center">{{item.working_type}}</uni-td>
								<uni-td align="center">{{item.working_time}}</uni-td>
								<uni-td align="center">{{item.start_time}}</uni-td>
								<uni-td align="center">{{item.end_time}}</uni-td>
								<uni-td align="center"></uni-td>
							</uni-tr>
						</uni-table>
					</view>
				</view>
			</view>
			<view class="inner" v-if="showThird">
				<view class="title">工人详情</view>
				<view class="close" @click="close">
					<image src="/static/close_white.png" mode="widthFix"></image>
				</view>
				<view class="box">
					<view class="base">
						<view class="out_line flex flex_start">
							<view class="line flex flex_start">
								<view class="tit">工人姓名：</view>
								<view class="text">{{currentWorkerInfo.worker_name}}</view>
							</view>
							<view class="line flex flex-start">
								<view class="tit">工人性别：</view>
								<view class="text">{{currentWorkerInfo.worker_gender=="male"?"男":"女"}}</view>
							</view>

						</view>
						<view class="out_line flex flex_start">
							<view class="line flex flex-start">
								<view class="tit">身份证号：</view>
								<view class="text">{{currentWorkerInfo.worker_id_card_number}}</view>
							</view>
							<view class="line flex flex-start">
								<view class="tit">出生日期：</view>
								<view class="text">{{currentWorkerInfo.worker_birthday}}</view>
							</view>
						</view>
						<view class="out_line flex flex_start">
							<view class="line flex flex-start">
								<view class="tit">手机号码：</view>
								<view class="text">{{currentWorkerInfo.worker_mobile}}</view>
							</view>
						</view>
					</view>
					<view class="tabs flex flex_start">
						<view class="tab" :class="currentSubTab==0?'active':''" @click="changeSubTab(0)">履职记录</view>
						<view class="tab" :class="currentSubTab==1?'active':''" @click="changeSubTab(1)">当前项目工资单</view>
					</view>
					<view class="table_wrap" v-if="currentSubTab==0">
						<uni-table border stripe emptyText="暂无更多数据" style="height:500px;">
							<uni-tr style="background: #f6f6f6;">
								<uni-th align="center"
									style="font-size: 14px;font-weight: 600;color:#333;">项目名称</uni-th>
								<uni-th align="center"
									style="font-size: 14px;font-weight: 600;color:#333;">用工主体</uni-th>
								<uni-th align="center"
									style="font-size: 14px;font-weight: 600;color:#333;">服务时间</uni-th>
								<uni-th align="center"
									style="font-size: 14px;font-weight: 600;color:#333;">服务地址</uni-th>
								<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;">状态</uni-th>
							</uni-tr>
							<uni-tr v-for="(item,index) in workerProjectRecordList" :key="index">
								<uni-td align="center">{{item.name}}</uni-td>
								<uni-td align="center">{{item.employer_name}}</uni-td>
								<uni-td align="center">{{item.work_start_time+"-"+item.work_end_time}}</uni-td>
								<uni-td align="center">{{item.work_address}}</uni-td>
								<uni-td align="center">
									<view class="status"></view>
								</uni-td>
							</uni-tr>
						</uni-table>
					</view>
					<view class="table_wrap" v-if="currentSubTab==1">
						<uni-table border stripe emptyText="暂无更多数据" style="height:500px;">
							<uni-tr style="background: #f6f6f6;">
								<uni-th align="center"
									style="font-size: 14px;font-weight: 600;color:#333;">工作时长</uni-th>
								<uni-th align="center"
									style="font-size: 14px;font-weight: 600;color:#333;">应发工资</uni-th>
								<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;">增加项</uni-th>
								<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;">扣减项</uni-th>
								<uni-th align="center"
									style="font-size: 14px;font-weight: 600;color:#333;">实发工资</uni-th>
								<uni-th align="center"
									style="font-size: 14px;font-weight: 600;color:#333;">发薪方式</uni-th>
								<uni-th align="center"
									style="font-size: 14px;font-weight: 600;color:#333;">支付时间</uni-th>
							</uni-tr>
							<uni-tr v-for="(item,index) in workerSalaryList" :key="index">
								<uni-td align="center">{{item.working_time}}</uni-td>
								<uni-td align="center">{{item.need_pay_salary}}</uni-td>
								<uni-td align="center">{{item.increase_salary}}</uni-td>
								<uni-td align="center">{{item.decrease_salary}}</uni-td>
								<uni-td align="center">{{item.real_pay_salary}}</uni-td>
								<uni-td align="center">{{item.send_type=="in_line"?"线上发薪":"线下结算"}}</uni-td>
								<uni-td align="center">{{item.update_time}}</uni-td>
							</uni-tr>
						</uni-table>
					</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import searchBox from "@/components/search_box.vue"
	import pageBox from "@/components/paging_box.vue"
	import commonData from "@/common/commonData.js"
	export default {
		name: "contract_list",
		data() {
			return {
				currentMainTab: 1,
				timeStr: "报名",
				list: [],
				tabs: [{
						value: "all-signing-requests",
						text: "全部"
					},
					{
						value: "pending-signing-requests",
						text: "已报名"
					},
					{
						value: "contracted-signing-requests",
						text: "待报到"
					},
					{
						value: "wait-signing-requests",
						text: "待签约"
					},
					{
						value: "entry-signing-requests",
						text: "已入职"
					},
					{
						value: "depart-signing-requests",
						text: "已离职"
					}
				],
				status: [{
						value: "pending",
						text: "待雇用"
					},
					{
						value: "rejected",
						text: "已拒绝"
					},
					{
						value: "signing_rejected",
						text: "报名拒绝"
					},
					{
						value: "signing_expired",
						text: "已失效"
					},
					{
						value: "approved",
						text: "待签约"
					},
					{
						value: "worker_rejected",
						text: "工人拒绝"
					},
					{
						value: "contracted",
						text: "待报到"
					},
					{
						value: "contract_expired",
						text: "已失效"
					},
					{
						value: "fail",
						text: "失约"
					},
					{
						value: "entry",
						text: "已入职"
					},
					{
						value: "end",
						text: "项目完结"
					},
					{
						value: "depart",
						text: "已离职"
					}
				],
				pagination: {},
				showMask: false,
				showFirst: false,
				showSecond: false,
				showThird: false,
				seleAll: false,
				selectedIds: [],
				selectedIndex: [],
				currentTab: {
					value: "all-signing-requests",
					text: "全部"
				},
				currentPage: 1,
				id: "",
				currentInfo: {
					"broker_mobile": "",
					"broker_name": "",
					"code": "",
					"content": "",
					"id": "",
					"name": "",
					"work_address": "",
					"worker_salary": "",
					"worker_welfare": ""
				},
				waitDepartList: [], //待离职名单
				searEmployee: "",
				searStart: "",
				searEnd: "",
				currentWorker: {},
				currentWorkerInfo: {},
				workerCheckinList: [],
				workerProjectRecordList: [],
				workerSalaryList: [],
				currentSubTab: 0,
				checkStatus: [{
						value: "to_be_settled",
						text: "待结算"
					},
					{
						value: "to_be_issued",
						text: "待发放"
					},
					{
						value: "issued",
						text: "已发放"
					},
					{
						value: "voided",
						text: "已作废"
					},
				],
				salaryHandlePeriod: commonData.paySalaryPeriod,
				period: commonData.periodList,
				showMoreInfo: false,
				moreText: "展开全部"
			};
		},
		components: {
			searchBox,
			pageBox
		},
		created() {
			let paramArr = []
			if (location.href.indexOf("?") != -1) {
				paramArr = location.href.split("?")[1].split("&")
			}
			for (var i = 0; i < paramArr.length; i++) {
				if (paramArr[i].indexOf("id") != -1) {
					this.id = paramArr[i].split("=")[1]
				}
			}
			// this.getList()
			this.getInfo()
		},
		methods: {
			back() {
				this.setPageName({
					pageName: "project_list"
				}, "", "all")
				this.$emit("getPageName", {
					pageName: "project_list"
				})
			},
			changeMainTab(index) {
				this.currentMainTab = index
			},
			showMore() {
				let _this = this
				this.showMoreInfo = !this.showMoreInfo
				if (this.showMoreInfo) {
					this.moreText = "收起"
				} else {
					this.moreText = "展开全部"
				}
			},
			toNext(e) {
				this.currentPage = e
				this.resetSeleData()
				this.getList()
			},
			showWorkerDetail(item) {
				let url = "/broker/worker/" + item.id + "/info"
				this.$request(url).then(res => {
					if (res.code == 0) {
						this.currentWorkerInfo = res.data
						this.showMask = true
						this.showThird = true
						this.getWorkerProjectRecord(item.worker_id)
						this.getWorkerSalaryList(item.id)
					}
				})
			},
			getWorkerProjectRecord(id) {
				let url = "/broker/worker/" + id + "/request/list"
				this.$request(url).then(res => {
					if (res.code == 0) {
						this.workerProjectRecordList = res.data.list
					}
				})
			},
			getWorkerSalaryList(id) {
				let url = "/broker/project/salary/" + id + "/list"
				this.$request(url).then(res => {
					if (res.code == 0) {
						this.workerSalaryList = res.data.list
					}
				})
			},
			changeTab(item) {
				this.currentTab.value = item.value
				this.currentTab.text = item.text
				this.getList()
				// this.resetSeleData()
			},
			resetSeleData() {
				this.selectedIds = [];
				this.selectedIndex = [];
				this.seleAll = false
			},
			changeSubTab(index) {
				this.currentSubTab = index
			},
			addToList(ids) {
				let id = ids
				console.log(id)
				// 如果存在就移除，不存在就push
				if (this.selectedIds.indexOf(id) == -1) {
					this.selectedIds.push(id)
				} else {
					this.selectedIds.splice(this.selectedIds.indexOf(id), 1)
				}
				if (this.selectedIds.length == this.list.length) {
					this.seleAll = true
				} else {
					this.seleAll = false
				}
				console.log(this.selectedIds)
			},
			checkAll() {
				this.seleAll = !this.seleAll
				if (this.seleAll) {
					let len = this.list.length;
					let arr = []
					let indexArr = []
					for (var i = 0; i < len; i++) {
						arr.push(this.list[i].id)
						indexArr.push(i)
					}
					this.selectedIds = arr
					this.selectedIndex = indexArr
				} else {
					this.selectedIds = []
					this.selectedIndex = []
				}
			},
			navigate(obj, id) {
				this.setPageName(obj, id)
			},
			getList() {
				let url = "/broker/project/" + this.id + "/" + this.currentTab.value + "?page=" + this.currentPage +
					"&worker_search=" + this.searEmployee + "&start_time=" + this.searStart + "&end_time=" + this.searEnd +
					"&page_size=15"
				this.$request(url).then(res => {
					if (res.code == 0) {
						this.list = res.data.list
						this.pagination = res.data.pagination
					}
				})
			},
			showRecord(item) {
				this.currentWorker = item
				let url = "/broker/attendance-record/" + this.id + "/" + item.worker_id
				this.$request(url).then(res => {
					if (res.code == 0) {
						this.showMask = true
						this.showSecond = true
						this.workerCheckinList = res.data
					}
				})
			},
			getInfo() {
				let url = "/admin/project/" + this.id
				this.$request(url).then(res => {
					if (res.code == 0) {
						this.currentInfo = res.data
					}
				})
			},
			handleSearch(e) {
				this.searEmployee = e.employeeName
				this.searStart = e.startTime
				this.searEnd = e.endTime
				this.getList()
			},
			batchPass(id, confrimType) {
				let tips = confrimType ? "已通过" : "已拒绝";
				let arr = []
				let data = {
					"relation_id": [], // 选择数据ID
					"project_id": this.id, // 项目ID
					"confirm": confrimType, // true 审核 false 拒绝
					"remark": "" // 备注 （非必填）
				}
				if (id) {
					arr.push(id)
					data.relation_id = arr
				} else {
					data.relation_id = this.selectedIds
				}
				this.$request("/broker/project/approve", data, "POST").then(res => {
					if (res.code == 0) {
						uni.showToast({
							title: tips,
							duration: 2000,
							icon: "none"
						})
						this.resetSeleData()
						this.getList()
					}
				})
			},
			batchCheckin(id, confrimType) {
				let tips = confrimType ? "已报到" : "已标记失约";
				let arr = []
				let data = {
					"relation_id": [], // 选择数据ID
					"project_id": this.id, // 项目ID
					"confirm": confrimType, // true 审核 false 拒绝
					"remark": "" // 备注 （非必填）
				}
				if (id) {
					arr.push(id)
					data.relation_id = arr
				} else {
					data.relation_id = this.selectedIds
				}
				this.$request("/broker/project/confirm-entry", data, "POST").then(res => {
					if (res.code == 0) {
						uni.showToast({
							title: tips,
							duration: 4000,
							icon: "none"
						})
						this.resetSeleData()
						this.getList()
					}
				})
			},
			batchSign(id, confrimType) {
				let tips = confrimType ? "已签约" : "已标记未签";
				let arr = []
				let data = {
					"relation_id": [], // 选择数据ID
					"project_id": this.id, // 项目ID
					"confirm": confrimType, // true 审核 false 拒绝
					"remark": "" // 备注 （非必填）
				}
				if (id) {
					arr.push(id)
					data.relation_id = arr
				} else {
					data.relation_id = this.selectedIds
				}
				this.$request("/broker/project/contract", data, "POST").then(res => {
					if (res.code == 0) {
						uni.showToast({
							title: tips,
							duration: 4000,
							icon: "none"
						})
						this.resetSeleData()
						this.getList()
					}
				})
			},
			close() {
				this.showMask = false
				this.showFirst = false
				this.showSecond = false
				this.showThird = false
			},
			batchDepart() {
				if (this.selectedIds.length == 0) {
					uni.showToast({
						title: "您还未选择数据",
						icon: "error",
						duration: 2000
					})
					return
				}
				let arr = []
				let len = this.list.length
				let seleLen = this.selectedIds.length
				console.log(this.list, this.selectedIds)
				for (var i = 0; i < len; i++) {
					for (var j = 0; j < seleLen; j++) {
						if (this.list[i].id == this.selectedIds[j]) {
							arr.push(this.list[i])
						}
					}
				}
				this.waitDepartList = arr
				this.showMask = true
				this.showFirst = true
			},
			deleteData(id) {
				this.selectedIds.splice(this.selectedIds.indexOf(id), 1)
				this.batchDepart()
				if (this.selectedIds == 0) {
					this.close()
					this.seleAll = false
				}
			},
			sureDepart() {
				let _this = this
				uni.showModal({
					title: "正在提交离职申请",
					content: "请确认离职名单是否正确，提交后无法修改，是否确认提交离职申请？",
					success(res) {
						if (res.confirm) {
							let data = {
								"ids": _this.selectedIds, // 已经选择数据ID
								"project_id": _this.id, // 项目ID
							}
							_this.$request("/broker/project/add-depart-user", data, "POST").then(resp => {
								if (resp.code == 0) {
									_this.resetSeleData()
									_this.getList()
									_this.close()
									uni.showModal({
										title: "申请已提交",
										content: "离职申请已通过公众号通知项目经理，请联系项目经理尽快审核离职名单。",
										showCancel: false
									})
								}
							})
						}
					}
				})
			},
			viewContract(id) {
				let url = "/broker/contract-signing-request/" + id + "/contract-detail-url"
				this.$request(url).then(resp => {
					if (resp.code == 0) {
						window.open(resp.data.contract_url)
					}
				})
			}
		}
	}
</script>

